.show-more-handle-button {
  text-decoration: none !important;
  text-align: center;
  border: 1px solid white;
  display: inline-block;
  padding: 2px 16px 3px;
  border-radius: 3px;
  cursor: pointer;
  border-color: var(--mainColor);
  color: var(--mainColor);
}


/*.show-more-handle-button,*/
.ecp-button {
  text-decoration: none !important;
  text-align: center;
  border: 1px solid white;
  display: inline-block;
  padding: 0;
  border-radius: 3px;
  cursor: pointer;
  border-color: var(--mainColor);
  color: var(--mainColor);
  
  a {
    text-decoration: none;
    display: inline-block;
    padding: 2px 16px 3px;

    &:hover {
      color: white !important;
      border-color: transparent;
      background-color: var(--mainColor);
    }
  }

  &.why-us {
    border-color: var(--whyUsColor);
    a {
      color: var(--whyUsColor);

      &:hover {
        background-color: var(--whyUsColor);
      }
    }
  }

  &.way-we-teach {
    border-color: var(--wayWeTeachColor);
    a {
      color: var(--wayWeTeachColor);

      &:hover {
        background-color: var(--wayWeTeachColor);
      }
    }
  }

  &.daily-life {
    border-color: var(--dailyLifeColor);
    a {
      color: var(--dailyLifeColor);

      &:hover {
        background-color: var(--dailyLifeColor);
      }
    }
  }

  &.our-results {
    border-color: var(--ourResultsColor);
    a {
      color: var(--ourResultsColor);

      &:hover {
        background-color: var(--ourResultsColor);
      }
    }
  }

  &.filled a {
    color: white;
  }

  &.why-us.filled { background-color: var(--whyUsColor); }
  &.way-we-teach.filled { background-color: var(--wayWeTeachColor); }
  &.daily-life.filled { background-color: var(--dailyLifeColor); }
  &.our-results.filled { background-color: var(--ourResultsColor); }
  &.why-us.filled a:hover { background-color: color(var(--whyUsColor) blackness(+15%)); }
  &.way-we-teach.filled a:hover { background-color: color(var(--wayWeTeachColor) blackness(+15%)); }
  &.daily-life.filled a:hover { background-color: color(var(--dailyLifeColor) blackness(+15%)); }
  &.our-results.filled a:hover { background-color: color(var(--ourResultsColor) blackness(+15%)); }
}