@import './variables';
/*@import './menu';*/

body #content #toggleable-menu {
  position: relative;
}


body.single-post #content #toggleable-menu,
body.home #content #toggleable-menu,
body.search #content #toggleable-menu,
body.page #content #toggleable-menu {
  li li {
    &:not(.tagged-important) { display: none; }
  }
}


#content {
  #toggleable-menu {
    width: 100%;
    top: 0;
    z-index: 1000;
    background-color: rgba(255, 255, 255, 1);

    a {
      @apply --mainFont;
    }

    .sub-menu {
      height: 0;
      visibility: hidden;
      /*transition: height 0.5s ease;*/
      padding-bottom: 0;
      z-index: -10;
      font-size: 0.85rem;

      li a {
        padding: 3px 6px;
        font-size: var(--smallerFontSize);
        color: black;
        background-color: white;
        width: 100%;
        display: block;

        &:hover {
          background-color: #eee;
        }
      }
    }

    &.is-visible .sub-menu {
      height: auto;
      visibility: visible;
      transition: height 0.5s ease;
      padding-bottom: var(--pad);
      z-index: 100;
      position: inherit;
    }

    #header-menu {
      list-style-type: none;
      margin: 0;
      @apply --pageWidth;
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      position: relative;
      align-items: flex-start;

      a {
        text-decoration: none;
        /*color: white;*/
      }

      > li {
        width: 25%;
        text-align: center;
        font-weight: bold;
        padding: 0;
        position: relative;

        &:hover .sub-menu {
          height: auto;
          visibility: visible;
          z-index: 30;
          position: absolute;
          width: 100%;
          background-color: white;

          @media (--fit-screen) {
            visibility: hidden;
            z-index: -10;
          }
        }

        > a {
          display: block;
          /*padding-bottom: calc(var(--pad) * 0.75);*/
          padding: 0.6rem 1rem 0.8rem;
          margin: 0;
          @apply --mainFont;
          font-weight: bold;
          font-size: 1rem;
          border-bottom-width: 0;
          /*border-bottom-style: solid;*/
          &[href*="why-us"],
          &[href*="o-nas"] { color: var(--whyUsColor); }

          &[href*="way-we-teach"],
          &[href*="jak-ucime"] { color: var(--wayWeTeachColor); }

          &[href*="daily-life"],
          &[href*="aktivity"] { color: var(--dailyLifeColor); }

          &[href*="our-results"],
          &[href*="nase-vysledky"] { color: var(--ourResultsColor); }
        }

        &:hover > a {
          color: white;

          &[href*="why-us"],
          &[href*="o-nas"] { background-color: var(--whyUsColor); }

          &[href*="way-we-teach"],
          &[href*="jak-ucime"] { background-color: var(--wayWeTeachColor); }

          &[href*="daily-life"],
          &[href*="aktivity"] { background-color: var(--dailyLifeColor); }

          &[href*="our-results"],
          &[href*="nase-vysledky"] { background-color: var(--ourResultsColor); }
        }

        &.current-menu-item {
          & > a {
            color: white;

            &[href*="why-us"],
            &[href*="o-nas"] { background-color: var(--whyUsColor); }

            &[href*="way-we-teach"],
            &[href*="jak-ucime"] { background-color: var(--wayWeTeachColor); }

            &[href*="daily-life"],
            &[href*="aktivity"] { background-color: var(--dailyLifeColor); }

            &[href*="our-results"],
            &[href*="nase-vysledky"] { background-color: var(--ourResultsColor); }
          }
        }

        @media (--small-screen) {
          width: 100%;
        }

        @media (--medium-screen) {
          width: 50%;
        }
      }

      /*underscore the main menu item*/
      /*was here*/

      li {
        /*display: inline-block;*/
        font-size: 1.2rem;
        /*font-weight: bold;*/
        line-height: 1.8em;

        ul {
          list-style-type: none;
          margin: 0;
          /*padding-top: calc(var(--pad) * 0.75);*/
        }

        li {
          /*display: none;*/
          font-size: var(--smallerFontSize);
          /*font-weight: normal;*/
          line-height: 1.8rem;

          a {
            padding: 8px 5px;
          }
        }
      }
    }
  }
}
