:root {

  /*typography*/
  /*--headlineFont: serif;*/
  --baseFontSize: 16px;
  --baseLineHeight: 1.6;
  --smallerFontSize: 0.85rem;
  --accessibilityFontSize: 0.85rem;

  --mainFont: {
    font-family: 'Open Sans', sans-serif;
    line-height: var(--baseLineHeight);
    font-weight: 400;
    font-stretch: normal;
    font-size: var(--baseFontSize);
  }

  --headlineFont: {
    /*font-family: 'Playfair Display', serif;*/
    /*font-family: 'Montserrat', sans-serif;*/
    font-family: 'Ubuntu', sans-serif;
    /* font-family: 'museo-sans', sans-serif; */
    line-height: var(--baseLineHeight);
    /*font-weight: bold;*/
    font-weight: 700;
    font-stretch: normal;
    /*font-size: var(--baseFontSize);*/
  }

  /*padding*/
  --pad: 1rem;

  /*colors*/
  --mainColor: rgb(37, 34, 89); /* rgb(37, 37, 91); 253456 = 37, 52, 86 */
  --secondaryColor: rgb(158, 0, 18);
  --textColor: #333;
  --dimmedColor: #666;
  --dimmedBackgroundColor: #eee;
  --tableHeader: #eee;

  /*section specific*/
  --whyUsColor: rgb(104, 44, 128);
  --wayWeTeachColor: rgb(29, 134, 193);
  --dailyLifeColor: rgb(17, 173, 151);
  --ourResultsColor: rgb(172, 158, 51);

  /*measures*/
  --siteWidth: 980px; /* UPDATE CUSTOM MEDIA, TOO */

  /*snippets*/
  --pageWidth: {
    width: 100%;
    max-width: var(--siteWidth);
    margin: 0 auto;
    padding: 0 var(--pad);
  }

  --blogPageWidth: {
    width: 100%;
    max-width: calc(var(--siteWidth) * 0.75);
    margin: 0 auto;
    padding: 0 var(--pad);
  }

  --paddingTopBottomFull: {
    padding-top: var(--pad);
    padding-bottom: var(--pad);
  }

  --paddingTopBottom: {
    padding-top: calc(var(--pad) / 2);
    padding-bottom: calc(var(--pad) / 2);
  }

  --fix: {
    &::after {
      content: "";
      display: table;
      clear: both;
    }
  }

  --buttonstyle: {
    text-decoration: none;
    display: inline-block;
    color: rgba(255, 255, 255, 0.8);
    padding: 3px 10px 5px;
    border: 1px solid rgba(255, 255, 255, 0.4);
    border-radius: 2px;
    margin-top: 8px;
    cursor: pointer;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.4);

    &:hover {
      border: 1px solid rgba(255, 255, 255, 0.75);
      color: rgba(255, 255, 255, 1);
      box-shadow: 0 0 0 rgba(0, 0, 0, 0);
    }
  }
}

@custom-media --small-screen only screen and (width <= 640px);

@custom-media --semilarge-screen only screen and (width <= 720px);

@custom-media --medium-screen only screen and (width > 640px) and (width <= 980px);

@custom-media --large-screen only screen and (width > 980px);

@custom-media --fit-screen only screen and (width < 980px);

@custom-media --trash-to-top only screen and (width < 1048px);
