@import './variables';

table.key-facts-css,
table.key-facts-universities-css {
  /*display: block;*/
  width: 100%;
  margin-top: var(--pad);
  margin-bottom: var(--pad);

  caption { display: none; }

  &.tablepress thead th {
    border-bottom: none;
    background-color: transparent !important;
    font-size: 1.35em;
    @apply --headlineFont;
    line-height: 1em;
    padding: calc(var(--pad) * 1.5) var(--pad) 2px;
    /*padding-top: calc(var(--pad) * 1.5);*/
    /*padding-bottom: 2px;*/
    /*border-top: 1px solid grey;*/
    text-align: center;
    /*padding-left: var(--pad);*/
    /*padding-right: var(--pad);*/
  }

  &.tablepress tbody td {
    font-size: 0.75rem;
    line-height: 1em;
    padding: 2px var(--pad) calc(var(--pad) * 1.5);
    /*padding-top: 2px;*/
    /*padding-bottom: calc(var(--pad) * 1.5);*/
    /*border-bottom: 1px solid grey;*/
    text-align: center;
    /*padding-left: var(--pad);*/
    /*padding-right: var(--pad);*/
  }
}


.tablepress {
  thead th { border-top: 1px solid grey; }
  tbody td { border-bottom: 1px solid grey; }
}

.tablepress.why-us {
  thead th { border-top: 1px solid var(--whyUsColor); }
  tbody td { border-bottom: 1px solid var(--whyUsColor); }
}

.tablepress.way-we-teach {
  thead th { border-top: 1px solid var(--wayWeTeachColor); }
  tbody td { border-bottom: 1px solid var(--wayWeTeachColor); }
}

.tablepress.daily-life {
  thead th { border-top: 1px solid var(--dailyLifeColor); }
  tbody td { border-bottom: 1px solid var(--dailyLifeColor); }
}

.tablepress.our-results {
  thead th { border-top: 1px solid var(--ourResultsColor); }
  tbody td { border-bottom: 1px solid var(--ourResultsColor); }
}

table.key-facts-universities-css {
  &.tablepress thead th {
    font-size: 1em;
  }

  &.tablepress tbody td {
    font-size: 0.9rem;
  }
}
