
.subheader-submenu {
  /*background-color: #eee;*/
  /*border-bottom: 1px solid #eee;*/

  nav {
    @apply --pageWidth;
    display: flex;
    flex-wrap: wrap;
    align-items: stretch;

    @media (--fit-screen) {
      padding: 0;
    }
  }

  .item {
    flex-wrap: nowrap;
    width: 25%;
    text-align: center;
    background-color: white;
    font-weight: bold;
    border: 1px solid var(--dimmedBackgroundColor);
    border-right: 0;
    position: relative;

    a {
      position: relative;
      display: block;
      width: 100%;
      /*top: 50%;*/
      left: 0;
      /*transform: translateY(-50%);*/
      text-decoration: none;
      padding: calc(var(--pad) * 1.5) var(--pad);
    }

    &:last-of-type { border-right: 1px solid var(--dimmedBackgroundColor); }

    &.active {
      border: 0;
      a { color: white; }
    }

    @media (--small-screen) {
      width: 50%;

      &:nth-of-type(1),
      &:nth-of-type(2) { border-bottom: 0; }

      &:nth-of-type(2) { border-right: 1px solid var(--dimmedBackgroundColor); }
    }
  }

  .why-us {
    a { color: var(--whyUsColor); }
    &.active { background-color: var(--whyUsColor); }
  }

  .way-we-teach {
    a { color: var(--wayWeTeachColor); }
    &.active { background-color: var(--wayWeTeachColor); }
  }

  .daily-life {
    a { color: var(--dailyLifeColor); }
    &.active { background-color: var(--dailyLifeColor); }
  }

  .our-results {
    a { color: var(--ourResultsColor); }
    &.active { background-color: var(--ourResultsColor); }
  }
}
