@import './variables';

.one-half-column,
.first-half-column,
.second-half-column {
  /*padding: calc(var(--pad) * 2) 0;*/
  padding: 0;
  /*width: calc(50% - var(--pad) * 2);*/
  width: calc(50% - var(--pad) * 2 - 5px);
  margin: 0 var(--pad);
  display: inline-block;
  vertical-align: top;
  /*float: left;*/
  @media (--semilarge-screen) {
    width: 100%;
    margin-left: 0;
    margin-right: 0;
  }
}

.first-half-column {
  margin-left: 0;
  margin-right: calc(var(--pad) * 2);

  &::before {
    content: "";
    display: table;
    clear: both;
  }
}

.second-half-column {
  margin-right: 0;
  margin-left: calc(var(--pad) * 2);
  @apply --fix;
}

.one-half-column,
.first-half-column,
.second-half-column {
  @media (--semilarge-screen) {
    margin-left: 0;
    margin-right: 0;
  }
}


.text_left_image_right_section {
  @apply --fix;

  /*padding-top: calc(var(--pad) * 3);*/

  & > * {
    width: 66%;

    @media (--semilarge-screen) {
      width: 100%;
    }
  }

  h3 {
    padding-top: calc(var(--pad) * 2);
  }

  h3,
  h4 {
    width: 100%;
    clear: both;
  }

  img:first-of-type {
    float: right;
    width: 30%;
    height: auto;
    margin-top: 12px;

    @media (--semilarge-screen) {
      display: none;
    }
  }
}

.row-box {
  @apply --pageWidth;
  padding: 0;
  display: flex;
  flex-wrap: wrap;

  .one-third,
  .two-thirds {
    img {
      max-height: 90vh;
      width: auto;
    }
  }

  .one-third {
    width: 33.33%;
    padding: var(--pad);

    @media (--semilarge-screen) {
      width: 100%;
      margin: 0;
      padding-left: 0;
      padding-right: 0;
    }
  }

  .two-thirds {
    width: 66.66%;
    padding: var(--pad);

    @media (--semilarge-screen) {
      width: 100%;
      margin: 0;
      padding-left: 0;
      padding-right: 0;
    }
  }

  div:first-of-type {
    margin-left: calc(-1 * var(--pad));
    margin-right: var(--pad);

    @media (--semilarge-screen) {
      margin: 0;
      padding-left: 0;
      padding-right: 0;
    }
  }

  div:last-of-type {
    margin-right: calc(-1 * var(--pad));
    margin-left: var(--pad);

    @media (--semilarge-screen) {
      margin: 0;
      padding-left: 0;
      padding-right: 0;
    }
  }
}

