@import './variables';

html { box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit;
}

html { border-collapse: collapse; }
* { border-collapse: inherit; }

html,
body {
  margin: 0;
  padding: 0;
  height: 100%;
}

*:not(body) {
  background-repeat: no-repeat;
  background-position: 50%;
  background-size: cover;
}

h1,
h2,
h3,
h4,
h5 {
  @apply --headlineFont;
}

h1 {
  font-size: 1.8em;
  padding: calc(var(--pad) * 2) 0 var(--pad);
}

h2 {
  font-size: 1.4em;
  padding: var(--pad) 0 calc(var(--pad) / 2);
}

h3 {
  font-size: 1.4em;
  font-weight: 400;
  padding-bottom: calc(var(--pad) / 2);
}

h4 {
  font-size: 1.2em;
}

ul,
menu,
dir {
  list-style-type: disc;
  margin-left: var(--pad);
}

li {
  display: list-item;
}

p {
  font-weight: 400;
}
