@import './variables';

#footer-menu-ecp {
  ul.menu {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    position: relative;
    align-items: flex-start;

    a {
      text-decoration: none;
      color: white;
    }

    > li {
      width: 25%;
      text-align: center;
      font-weight: bold;
      padding: 0;

      @media (--small-screen) {
        width: 100%;
      }

      @media (--medium-screen) {
        width: 50%;
      }

      > a {
        display: block;
        padding: calc(var(--pad) * 1) var(--pad) calc(var(--pad) * 1.2);
        margin: 0;
        @apply --headlineFont;
        /*font-weight: bold;*/
        font-size: 1rem;
        border-bottom: 2px solid white;
        /*border-bottom-style: solid;*/
        &[href*="why-us"],
        &[href*="o-nas"] { border-bottom-color: var(--whyUsColor); }

        &[href*="way-we-teach"],
        &[href*="jak-ucime"] { border-bottom-color: var(--wayWeTeachColor); }

        &[href*="daily-life"],
        &[href*="aktivity"] { border-bottom-color: var(--dailyLifeColor); }

        &[href*="our-results"],
        &[href*="nase-vysledky"] { border-bottom-color: var(--ourResultsColor); }
      }

      ul {
        list-style-type: none;
        margin: 0;
        padding-top: calc(var(--pad) / 2);

        > li {
          a {
            font-weight: normal;
            font-size: var(--smallerFontSize);
            padding: calc(var(--pad) / 4) calc(var(--pad) / 2);
            color: rgba(255, 255, 255, 0.7);

            &:hover {
              color: rgba(255, 255, 255, 1);
            }
          }
        }
      }
    }
  }
}

