/* fix autoprefixer!*/

aside#secondary {
  display: none;
}

/*styles for post type*/

.entry-content .news-list div { padding-bottom: 0; }

.news-list {
  /*div { padding-bottom: 0; }*/
  .one-post-in-list {
    display: flex;
    flex-direction: row;
    border-top: 1px solid var(--secondaryColor);
    padding: calc(var(--pad) / 2) 0 !important;

    &:last-of-type {
      border-bottom: 1px solid var(--secondaryColor);
    }

    a {
      display: inherit;
      text-decoration: none;
      color: var(--dimmedColor);
    }

    .featured-image-in-list {
      width: 30%;

      img {
        display: inherit;
        width: 100%;
        height: auto;
      }
    }

    .info-in-list {
      width: 70%;
      display: flex;
      flex-direction: column;
      justify-content: center;
      padding-left: var(--pad);

      .date-in-list {
        @apply --mainFont;
        font-size: var(--smallerFontSize);
        font-weight: bold;
      }

      h4 {
        @apply --mainFont;
        font-size: 1.2rem;
      }
    }
  }
}

/*Tables*/

.tablepress tfoot th,
.tablepress thead th {
  background-color: var(--tableHeader) !important;
}

.ecp-table-facts {
  width: 100%;
  overflow-x: scroll;
  overflow-y: hidden;

  .tablepress {
    min-width: 700px;
  }
}


/*show more*/

/*.expandable {
  display: none;
  transition: all 0.45s cubic-bezier(0.44, 0.99, 0.48, 1);

  &.expanded {
    display: block;
    transition: all 0.45s cubic-bezier(0.44, 0.99, 0.48, 1);
  }
}*/

a.show-more-handle {
  & + .expandable {
    display: none;
    transition: all 0.45s cubic-bezier(0.44, 0.99, 0.48, 1);
  }

  &.expanded {
    & + .expandable {
      display: block;
    }
  }
}


a.show-more-handle-normal {
  font-style: italic;
}


