/*Fix table press*/
.tablepress {
  table-layout: fixed;
}

.dataTables_wrapper,
.dataTables_wrapper .tablepress {
  clear: none !important;
}

.spacer-tagg {
  height: var(--pad);
  &.size-2 { height: calc(var(--pad) * 2); }
  &.size-3 { height: calc(var(--pad) * 3); }
  &.size-4 { height: calc(var(--pad) * 4); }
}


