
#lang-switcher {
  margin-right: var(--pad);

  a {
    color: white;
    text-decoration: none;
  }

  ul {
    list-style-type: none;
    margin: 0;
    margin-top: 7px;

    li {
      padding: 3px 10px 4px;
      background-color: color(var(--mainColor) blackness(+15%));
      display: none;

      &.lang-item-first::after {
        content: "/";
        display: inline-block;
        margin-left: 8px;
        font-weight: normal;
      }

      &.current-lang {
        display: inline-block;
        background-color: transparent;
      }

      &:hover {
        font-weight: bold;
      }
    }

    &:hover {
      li {
        display: block;
        background-color: color(var(--mainColor) blackness(+15%));
      }
    }
  }
}
