
#slider-container {
  height: calc(100vh - 103px - 32px - 60px);
  position: relative;

  /*div.carpet:first-of-type {
    visibility: visible;
  }*/
}

.carpet {
  height: calc(100vh - 103px - 32px - 60px);
  width: 100%;
  text-align: center;
  position: absolute;
  background-size: cover, cover;
  opacity: 0;
  transition: opacity 2s ease;
  background-position: top;
  /*visibility: hidden;
  transition: visibility 1s ease;*/

  h1 {
    position: absolute;
    @apply --pageWidth;
    margin: 0 auto;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-style: italic;
    font-size: 2.2rem;
    line-height: 1.25;
    color: white;
    letter-spacing: 0.15rem;
    text-shadow: 0 0 15px rgba(0, 0, 0, 0.6);
  }
}

.to-next-section {
  position: absolute;
  padding: 0 8px;
  bottom: 24px;
  left: 50%;
  transform: translateX(-50%);
  display: block;
  border-radius: 4px;
  line-height: 2.3rem;
  background-color: rgba(255, 255, 255, 0.3);
  text-align: center;
  font-size: 22px;
  color: rgba(0, 0, 0, 0.5);
  text-decoration: none;
  z-index: 110;

  &:hover {
    background-color: rgba(255, 255, 255, 0.6);
    color: rgba(0, 0, 0, 0.8);
  }
}
