@import './variables';

/*4 sections*/

.four-main-bottom-headline {
  padding-left: calc(var(--pad) * 2);
  font-size: 1.2em;
  color: var(--dimmedColor);

  @media (--fit-screen) {
    text-align: center;
  }
}

.four-main-section {
  background-color: var(--dimmedBackgroundColor);
}

.four-main-hp,
.four-main-bottom {
  display: flex;
  flex-wrap: wrap;

  .section {
    margin: var(--pad);

    &::before {
      content: '';
      display: block;
      height: 240px;
      /* margin-left: var(--pad); */
      background-color: #ddd;
      background-size: cover;
    }

    &.why-us {
      h2 {
        background-color: var(--whyUsColor);
      }

      a {
        border-color: var(--whyUsColor);
        color: var(--whyUsColor);

        &:hover {
          background-color: var(--whyUsColor);
        }
      }
    }

    &.way-we-teach {
      h2 {
        background-color: var(--wayWeTeachColor);
      }

      a {
        border-color: var(--wayWeTeachColor);
        color: var(--wayWeTeachColor);

        &:hover {
          background-color: var(--wayWeTeachColor);
        }
      }
    }

    &.daily-life {
      h2 {
        background-color: var(--dailyLifeColor);
      }

      a {
        border-color: var(--dailyLifeColor);
        color: var(--dailyLifeColor);

        &:hover {
          background-color: var(--dailyLifeColor);
        }
      }
    }

    &.our-results {
      h2 {
        background-color: var(--ourResultsColor);
      }

      a {
        border-color: var(--ourResultsColor);
        color: var(--ourResultsColor);

        &:hover {
          background-color: var(--ourResultsColor);
        }
      }
    }

    h2 {
      text-align: center;
      background-color: #abc;
      margin: 0;
      margin-top: -80px;
      /* margin-right: var(--pad); */
      padding: 8px var(--pad);
      /*background-color: #999;*/
      color: white;
      font-size: 1.25rem;
      letter-spacing: 0.05rem;
    }

    div {
      text-align: center;
      /* margin-right: var(--pad); */
      padding: calc(var(--pad) * 1.5) calc(var(--pad) * 1.5) calc(var(--pad) * 3);
      background-color: white;
    }

    p {
      /*text-align: center;*/
      /*padding-bottom: calc(var(--pad) * 1.5);*/
    }

    hr {
      border: 0;
      color: transparent;
      background-color: transparent;
      margin-top: calc(var(--pad) * 1.5);
    }

    a {
      /*margin: 0 auto;*/
      /*border: 1px solid grey;*/
      /*margin-top: calc(var(--pad) * 1.5);*/
      /*display: inline-block;*/
      text-decoration: none;
      border: 1px solid white;
      padding: 3px 16px 5px;
      border-radius: 3px;

      &:hover {
        color: white;
        border: 0;
      }

      /*&::before {
        content: '';
        display: block;
        clear: both;
      }*/
    }
  }
}

.four-main-bottom {
  .hideme {
    display: none;
  }

  .section {
    width: calc(33.333% - 2 * var(--pad));

    h2 {
      font-size: 0.9em;
      margin-top: -60px;
    }

    div {
      font-size: 0.85em;
      padding: calc(var(--pad) * 1) calc(var(--pad) * 1) calc(var(--pad) * 2);
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
    }

    &::before {
      height: 180px;
    }

    @media (--fit-screen) {
      width: calc(100% - 2 * var(--pad));
      max-width: 400px;
      margin-left: auto;
      margin-right: auto;
      clear: both;
    }
  }

  @media (--fit-screen) {
    flex-direction: column;
  }
}

.four-main-hp {
  .section {
    width: calc(50% - 2 * var(--pad));

    @media (--semilarge-screen) {
      width: calc(100% - 2 * var(--pad));
    }
  }

  /*@media (--semilarge-screen) {
    width: calc(100% - 2 * var(--pad));
  }*/
}

.four-main-section.all-four-display {
  h3,
  .four-main-bottom {
    max-width: calc(var(--siteWidth) / 3 * 2);

    @media (--semilarge-screen) {
      max-width: var(--siteWidth);
    }
  }

  .four-main-bottom .section {
    width: calc(50% - 2 * 1rem);

    @media (--small-screen) {
      width: calc(100% - 2 * 1rem);
    }
  }
}



