
.people-box {
  @apply --pageWidth;
  display: flex;
  flex-wrap: wrap;
}

.person {
  /*display: inline-block;*/
  width: 33.33%;
  padding: calc(var(--pad) * 2);

  &:nth-child(3n) {
    padding-right: 0;
    padding-left: calc(4 * var(--pad));
  }

  &:nth-child(3n-5) {
    padding-left: 0;
    padding-right: calc(4 * var(--pad));
  }

  .img {
    margin-bottom: var(--pad);

    img {
      max-width: 180px;
      height: auto;
      box-shadow: 0 0 12px rgba(0, 0, 0, 0.2);
    }
  }

  .text {
    font-size: 0.9em;
    color: var(--dimmedColor);
    font-style: italic;
    padding-bottom: 0;
    margin-bottom: 0;
  }

  .name {
    margin-top: var(--pad);
    margin-bottom: 0;
    padding-bottom: 0;
    font-weight: bold;
  }

  @media (--semilarge-screen) {
    width: 100%;
    margin-left: 0;
    margin-right: 0;

    .img {
      text-align: center;

      img {
        margin-left: auto;
        margin-right: auto;
      }
    }

    &:nth-child(3n) {
      margin-left: 0;
      margin-right: 0;
      padding: calc(var(--pad) * 2);
    }

    &:nth-child(3n-5) {
      margin-left: 0;
      margin-right: 0;
      padding: calc(var(--pad) * 2);
    }
  }
}
