@import './variables';

/*content*/

.full-width {
  padding: var(--pad);
  /*background-color: #ffd;*/

  p {
    @apply --pageWidth;
  }
}

.site-width {
  @apply --pageWidth;
  @apply --paddingTopBottom;
}

header.entry-header {
  @apply --pageWidth;
  padding-top: calc(var(--pad) * 2);
  padding-bottom: var(--pad);

  h1 {
    font-size: 1.25rem;
    letter-spacing: 0.07rem;
  }
}

.search-results #main,
.entry-content {
  @apply --pageWidth;

  p {
    padding: calc(var(--pad) / 2) 0;
    font-weight: 400;
  }

  img {
    max-width: 100%;
  }
  /*p,*/
  div:not(.full-width) {
    /*@apply --pageWidth;*/
    /*padding-bottom: var(--pad);*/
  }
}

.search-results #main {
  padding-bottom: calc(var(--pad) * 2);

  article {
    padding-top: calc(var(--pad) * 2);

    header.entry-header {
      padding: 0;

      h2 {
        padding: 0;
        font-size: 1.2rem;
        a { text-decoration: none; }
      }

      .entry-meta {
        font-style: italic;
        font-size: 0.9rem;
      }
    }

    .entry-summary {
      p { padding: 0; }
    }
  }

  nav.posts-navigation {
    padding-top: var(--pad);
    padding-bottom: var(--pad);
  }
}
