/*this is top level*/
.single-post {
  article {
    .post-featured-image {
      @apply --pageWidth;

      img {
        max-width: 100%;
        height: auto;
      }
    }
  }

  header.entry-header h1 {
    font-size: 2rem;
  }

  .entry-title {
    padding-bottom: 0;
  }

  .entry-content {
    /*apply blog width*/
    @apply --blogPageWidth;
    position: relative;

    .post-sharing-icons {
      position: absolute;
      top: 60px;
      left: -12%;
      width: 10%;
      text-align: center;
      display: flex;
      flex-direction: column;
      align-items: center;

      a {
        text-decoration: none;
        color: #aaa;

        &:hover {
          color: var(--dimmedColor);
        }
      }

      span.dashicons {
        display: inline-block;
        width: auto;
        height: auto;
        font-size: 28px;
        padding: calc(var(--pad) / 2);
        /*color: #aaa;
        cursor: pointer;

        &:hover {
          color: var(--dimmedColor);
        }*/
      }

      @media (--fit-screen) {
        position: relative;
        width: 100%;
        flex-direction: row;
        padding: var(--pad) 0;
        top: inherit;
        left: inherit;
        justify-content: center;
      }
    }

    .posted-on {
      font-size: var(--smallerFontSize);
      font-style: italic;
    }

    /*&::before {
      content: 'hulu';
      content: "\f301";
      font-family: dashicons;
      position: absolute;
      top: 60px;
      left: -12%;
      width: 10%;
      text-align: center;
    }*/
  }
}

.entry-footer {
  .edit-link {
    display: none;
  }
}

.post-navigation {
  margin-top: calc(var(--pad) * 1.5);
  margin-bottom: calc(var(--pad) * 3);
  padding: var(--pad) 0;

  .nav-links {
    @apply --blogPageWidth;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }

  .nav-previous,
  .nav-next {
    width: 40%;
  }

  .nav-next {
    text-align: right;
  }
}

