@import './variables';
/*@import './menu';*/

/*footer*/

footer.site-footer {
  background-color: var(--mainColor);
  color: white;

  .site-footer-content {
    @apply --pageWidth;
    padding-top: 0;
    padding-bottom: calc(var(--pad) * 2);
  }

  a {
    color: white;
  }

  h2 {
    display: none;
  }

  #menu-full-menu,
  #menu-full-menu-cz {
    list-style: none;
    /*@apply --inverseNavigation;*/
  }

  .site-info {
    background-color: var(--secondaryColor);
    color: white;
    text-align: center;
  }

  .site-info-content {
    @apply --pageWidth;
    @apply --paddingTopBottomFull;
    font-size: var(--smallerFontSize);

    span {
      white-space: nowrap;
    }
  }
}

#footer-social-icons,
.contactus-social-icons {
  font-family: ecpicons;
  font-size: 1.4rem;
  text-align: center;
  padding: var(--pad);

  a {
    display: inline-block;
    color: rgba(255, 255, 255, 0.7);
    padding: calc(var(--pad) / 2);
    margin: 0 calc(var(--pad) / 4);
    font-weight: normal;
    font-style: normal;
    font-variant: normal;
    text-transform: none;
    text-decoration: none;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    &:hover { color: rgba(255, 255, 255, 1); }
  }
}

#to-the-top {
  display: none;
}

.toTopShow #to-the-top {
  display: block;
  position: fixed;
  bottom: 30%;
  right: 26px;
  width: 42px;
  height: 42px;
  border-radius: 4px;
  line-height: 2.3rem;
  background-color: rgba(255, 255, 255, 0.3);
  text-align: center;
  font-size: 22px;
  color: rgba(0, 0, 0, 0.5);
  text-decoration: none;

  &:hover {
    background-color: rgba(255, 255, 255, 0.6);
    color: rgba(0, 0, 0, 0.8);
  }

  @media (--trash-to-top) {
    display: none !important;
  }
}


