
.pagepart {
  padding: calc(var(--pad) * 2) 0;
  background-color: var(--dimmedBackgroundColor);
  background-position: top;
  background-size: cover;

  h2,
  h1 {
    margin: 0;
    font-size: 1.3rem;
    padding-top: 0;
    /*margin-bottom: calc(var(--pad) * 2);*/
    margin-bottom: var(--pad);
    letter-spacing: 0.05rem;
  }

  ul {
    list-style: none;
  }

  h1 { font-size: 1.8rem; }

  li {
    padding: calc(var(--pad) / 2) 0;
    position: relative;
  }

  &.more-information {
    background-color: var(--mainColor);
    color: white;
    text-align: center;
    padding: calc(var(--pad) * 3) 0;

    .pagepart-content {
      text-align: center;
    }

    h2 {
      font-size: 1.8rem;
      letter-spacing: 0.1rem;
      margin-bottom: calc(var(--pad) / 2);
    }

    p {
      padding-bottom: calc(var(--pad) / 2);
    }

    a:not(.social) {
      display: inline-block;
      margin-top: calc(var(--pad) / 2);
      padding: calc(var(--pad) * 0.5) calc(var(--pad) * 1.5);
      background-color: var(--secondaryColor);
      color: white;
      border-radius: 3px;
      text-decoration: none;
      font-size: 1.15rem;

      &:hover {
        background-color: color(var(--secondaryColor) blackness(+15%));
      }
    }
  }

  &.daily-life,
  &.way-we-teach,
  &.why-choose,
  &.our-results,
  &.why-exciting {
    /*h2 { color: var(--whyUsColor); }*/

    li {
      /*padding: calc(var(--pad) / 2) 0 calc(var(--pad) / 2) 45px;*/
      padding-left: 45px;
      /*position: relative;*/

      &::before {
        content: "\f147";
        font-family: dashicons;
        display: inline-block;
        position: absolute;
        background-color: var(--whyUsColor);
        color: white;
        font-size: 27px;
        line-height: 30px;
        width: 30px;
        height: 30px;
        top: 5px;
        left: 0;
        border-radius: 15px;
      }
    }
  }

  &.way-we-teach {
    h1,
    h2 { color: var(--wayWeTeachColor); }
    li::before { background-color: var(--wayWeTeachColor); }
  }

  &.why-choose {
    h1,
    h2 { color: var(--whyUsColor); }
    li::before { background-color: var(--whyUsColor); }
  }

  &.our-results {
    h1,
    h2 { color: var(--ourResultsColor); }
    li::before { background-color: var(--ourResultsColor); }
  }

  &.why-exciting,
  &.daily-life {
    h1,
    h2 { color: var(--dailyLifeColor); }
    li::before { background-color: var(--dailyLifeColor); }
  }


  &.how-to-apply {
    background-color: var(--whyUsColor);
    color: white;
    padding: calc(var(--pad) * 2) 0;

    a { color: white; }

    ol {
      margin-left: 0;
      padding-left: 20px;
    }

    li {
      padding: calc(var(--pad) / 3) 0;
    }
  }

  .pagepart-content {
    @apply --pageWidth;
  }
}

.contactus-social-icons {
  padding-top: 0 !important;
  padding-bottom: 0 !important;
}
