@import './variables';

/*header*/
/*or #masthead*/

#toptop {
  background-color: var(--mainColor);
  font-size: var(--smallerFontSize);

  .lang-item {
    padding-left: 4px;
    padding-right: 4px;
    &.lang-item-en { padding-left: var(--pad); }
  }

  .lang-item-first::after {
    content: "/";
    color: rgba(255, 255, 255, 0.8);
    display: inline-block;
    margin-left: 8px;
    font-weight: normal;
  }

  a {
    color: rgba(255, 255, 255, 0.8);
    text-decoration: none;
    &:hover { color: rgba(255, 255, 255, 1); }
  }

  .menu-toptop-container {
    @apply --pageWidth;
  }

  ul {
    list-style-type: none;
    margin: 0;
    display: flex;
    justify-content: flex-end;
    align-items: center;
  }

  li {
    padding: calc(var(--pad) / 3) var(--pad);
    &:first-of-type { padding-left: 0; }
    &:last-of-type { padding-right: 0; }

    &.current-lang {
      font-weight: bold;
      a { color: rgba(255, 255, 255, 1); }
    }
  }
}


.headerbuttons {
  display: flex;
  flex-direction: column;
  align-items: flex-end;

  .first-line {
    display: flex;

    .search-box {
      margin-right: 4px;
      color: rgba(255, 255, 255, 0.9);
      font-size: 0.8rem;
      /*margin-top: 9px;*/

      #searchform {
        white-space: nowrap;
        label { display: none; }

        input#search {
          border: none;
          border-bottom: transparent;
          background-color: transparent;
          color: white;
          text-align: right;
          font-size: 0.85rem;
          transition: all 0.8s ease;
          /*width: 30px;*/
          &:focus {
            outline: none;
            border-bottom: 1px solid rgba(255, 255, 255, 0.4);
          }
        }

        input#searchsubmit {
          @apply --buttonstyle;
          background-color: var(--mainColor);
          border-color: transparent;
          box-shadow: none;
          font-family: dashicons;
          padding-top: 5px;
          padding-left: 5px;
          padding-right: 8px;
          font-size: 1rem;
          &:hover { border-color: transparent; }
        }

        &:hover {
          input#search {
            border-bottom: 1px solid rgba(255, 255, 255, 0.4);
          }
          /*input#search {
            width: 300px;
          }*/
        }
      }
    }
  }
}

header.site-header {
  @apply --paddingTopBottom;
  position: relative;
  background-color: var(--mainColor);
  color: inherit;

  .page-box {
    display: flex;
  }

  .site-branding {
    flex-grow: 1;
  }

  .site-logo {
    height: 80px;
    width: auto;
    max-width: 100%;
  }

  #site-navigation {
    color: white;

    button {
      @apply --buttonstyle;
      font-family: dashicons;
      margin-right: 1px;
      padding-top: 5px;

      &:hover {
        background-color: color(var(--mainColor) blackness(+15%));
      }
    }
  }
}


#menucontactus {
  text-align: right;
  font-size: 0.8rem;

  ul,
  li {
    display: inline-block;
    margin: 0;
    padding: 0;
  }

  a {
    @apply --buttonstyle;
    background-color: var(--secondaryColor);
    border: 1px solid var(--secondaryColor);

    &:hover {
      background-color: color(var(--secondaryColor) blackness(-15%));
      border: 1px solid rgba(255, 255, 255, 0.6);
    }
  }

  .news-events a {
    background-color: var(--mainColor);
    border: 1px solid rgba(255, 255, 255, 0.6);
    &:hover { background-color: color(var(--mainColor) blackness(+15%)); }
  }
}



